import React, { ReactElement, useState } from 'react';
import { useDispatch } from 'react-redux';

import { setIsOpen } from '../../../store/modal';
import Button from '../../atoms/button';
import { ISaveData } from '../../organisms/quiz/types';
import Chart from './chart';
import { Body, Container, EmptyMessage, Foot, Head } from './style';

export default function History(): ReactElement {
  const dispatch = useDispatch();
  const [historyList, setHistoryList] = useState(
    JSON.parse(window.localStorage.getItem('quiz') || '[]') as ISaveData[],
  );
  const onClose = () => dispatch(setIsOpen(false));
  const onRemove = () => {
    window.localStorage.removeItem('quiz');
    setHistoryList([]);
  };

  return (
    <Container>
      <Head>ÌÄ¥Ï¶à Í∏∞Î°ù</Head>
      <Body>
        {historyList.length ? (
          <Chart historyList={historyList} />
        ) : (
          <EmptyMessage>Í∏∞Î°ùÏù¥ Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§.</EmptyMessage>
        )}
      </Body>
      <Foot>
        {!!historyList.length && <Button size={'small'} children={'Í∏∞Î°ù Ï†ÑÏ≤¥ ÏÇ≠Ï†ú'} onClick={onRemove} />}
        <Button size={'small'} children={'Îã´Í∏∞'} onClick={onClose} />
      </Foot>
    </Container>
  );
}
